.post {
  width: 100%;
  border-radius: 10px;
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  margin: 30px 0;
  background-color: #16161a;
  position: relative;
  color: white;
}

.postPop {
  background-color: #16161a;
  z-index: 99;
  border: 1.5px solid #525263;
  position: absolute;
  top: 50px;
  right: 15px;
  overflow: hidden;

  border-radius: 8px;
  min-width: 50px;
  text-align: center;
  display: none;
}

.selected {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.selected__item {
  padding: 8px 16px;
  width: 100%;
  text-align: center;
}
.selected__item:hover {
  background-color: #525263;
}

.postPopShow {
  display: inline-block;
}

.postWrapper {
  padding: 10px;
}

.postTop {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.postTop:focus {
  border: 1px solid #e54f00;
}

.postTopRight {
  cursor: pointer;
}
.postTopLeft {
  display: flex;
  align-items: center;
}
.postProfileImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}

.postUsername {
  font-size: 15px;
  font-weight: 500;
  margin: 0 10px;
}

.postDate {
  font-size: 12px;
}

.postCenter {
  margin: 20px 0;
}

.postImg {
  margin-top: 20px;
  width: 100%;
}

.postBottom {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.postBottomLeft {
  display: flex;
  align-items: center;
}

.likeIcon {
  width: 24px;
  height: 24px;
  margin-right: 5px;
  cursor: pointer;
}

.postLikeCounter {
  font-size: 15px;
}

.postCommentText {
  cursor: pointer;
  border-bottom: 1px dashed gray;
  font-size: 15px;
}
