.topbarContainer {
  height: 50px;
  margin: 0 auto;
  max-width: 1280px;
  width: 100%;
  background-color: #25252c;

  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;
}

.userAvatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}

.topbarLeft {
  flex: 3;
}

.topbarLeft a {
  text-decoration: none;
}

.logo {
  font-size: 24px;
  margin-left: 20px;
  font-weight: bold;
  color: white;
  cursor: pointer;
}

a {
  text-decoration: none;
}

.logout {
  font-size: 16px;
  color: white;
  cursor: pointer;
  list-style: none;
  text-decoration: none;
}

.searchbar {
  width: 100%;
  height: 30px;
  background-color: white;
  border-radius: 30px;
  display: flex;
  align-items: center;
}

.searchIcon {
  font-size: 20px;
  margin-left: 10px;
}

.searchInput {
  border: none;
  width: 70%;
}

.searchInput:focus {
  outline: none;
}
.topbarCenter {
  flex: 5;
}

.topbarRight {
  flex: 4;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  color: white;
}

.topbarIconItem {
  margin-right: 15px;
  cursor: pointer;
  position: relative;
}

.topbarIconBadge {
  width: 15px;
  height: 15px;
  background-color: red;
  border-radius: 50%;
  position: absolute;
  top: -5px;
  right: -5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.topbarIcons {
  display: flex;
}
