.share {
  position: relative;
  width: 100%;
  border-radius: 10px;
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  background-color: #16161a;
  color: white;
}

.shareWrapper {
  padding: 10px;
}

.shareTop {
  overflow: hidden;
}

.shareHr {
  margin: 20px 0;
}
.shareInput {
  border: 16px;
  padding: 8px;
  border: none;
  background-color: #25252c;
  width: 100%;
  height: 70px;
  color: white;
  resize: none;
  height: 100px;
  /* white-space: pre-wrap;
  word-wrap: break-word;
  word-break: break-all; */
}

.shareInput:focus {
  outline: none;
}

.shareBottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.shareOptions {
  display: flex;
}

.shareOption {
  display: flex;
  align-items: center;
  margin-right: 15px;
  cursor: pointer;
}

.shareIcon {
  font-size: 18px;
  margin-right: 3px;
}

.shareOptionText {
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
}

.shareButton {
  width: 50px;
  color: white;
  cursor: pointer;
  border: none;
  padding: 7px;
  border-radius: 5px;
  background-color: rgb(32, 106, 218);
  font-weight: 500;
}

.shareImageContainer {
  width: 100%;
  display: flex;
  justify-content: center;
}

.shareImg {
  padding: 10px;
  max-width: 500px;
}

aside.emoji-picker-react {
  z-index: 99;
  position: absolute;
}
