.imageWrapper {
  flex: 7;
  min-height: 100vh;
  margin-right: 20px;
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 15px;
  justify-items: start;
  grid-auto-flow: row;
}

.portfolioImg {
  width: 100%;
  min-height: 300px;
  object-fit: cover;
  border-radius: 2px;
  transition: all 1s ease;
  overflow: hidden;
  opacity: 1;
}

.portfolioImg:hover {
  cursor: pointer;
  opacity: 0.9;
  transform: scale(1.05);
}
