.rightbar {
  flex: 2;
}

.rightbarWrapper {
  color: white;
}

.rightbarAd {
  width: 100%;
  border-radius: 10px;
  margin: 30px 0;
}

.rightbarTitle {
  margin-bottom: 20px;
}

.rightbarFriendList {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  color: white;
}

a {
  color: white;
}

.box {
  background-color: #18222d;
  color: #d1d5d9;
  min-height: 200px;
  width: 100%;

  border-radius: 10px;
  margin-bottom: 30px;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px;
  padding-top: 20px;
  justify-content: space-evenly;

  .light {
    cursor: pointer;
  }

  .dark {
    cursor: pointer;
  }
}
