.profile {
  display: flex;
  margin: 0 auto;
  max-width: 1280px;
}

.profileRight {
  flex: 9;
  background-color: #0c0c0c;
}

.profileCover {
  height: 320px;
  position: relative;
}

.profileCoverImg {
  width: 100%;
  height: 250px;
  object-fit: cover;
}

.profileRightBottom {
  display: flex;
}

.profileUserImg {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 150px;
  border: 3px solid white;
  cursor: pointer;
}

.profileInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profileInfoName {
  font-size: 24px;
  color: white;
  font-weight: bold;
}

.profileInfoDesc {
  font-weight: 300;
}

.rightbarTitle {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
}
.rightbarInfo {
  margin-bottom: 30px;
}

.rightbarInfoItem {
  margin-bottom: 10px;
}

.rightbarInfoKey {
  font-weight: 500;
  margin-right: 15px;
  color: #555;
}

.rightbarInfoValue {
  font-weight: 300;
}

.rightbarFollowingImg {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 5px;
}

.rightbarFollowings {
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-between; */
}

.rightbarFollowing {
  display: flex;
  flex-direction: column;
  margin: 16px;
  cursor: pointer;
}
