.formBasic {
  display: flex;
  flex-direction: column;
}

.form {
  padding: 0 20px;
}

.formWrapper {
  padding: 20px 200px;
  padding-bottom: 150px;
  padding-left: 50px;
  background-color: #18222d;
  width: 100%;
  display: flex;
  flex-direction: column;

  border-radius: 10px;

  .fieldItem {
    margin: 10px;
    outline: none;
    padding: 6px;
    border-radius: 6px;
  }

  .heading {
    display: flex;
    align-items: center;
    padding-top: 20px;
  }

  button {
    display: inline-block;
    margin-top: 50px;
  }
}

.userImg {
  height: 100px;
  margin: 20px 0;
  width: 100px;
}
